@font-face {
    font-display: swap;
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA LOGO FONT'), url('SEGA.TTF') format('truetype');
  }
  h1 {
    color: '#0ccac4';
    font-weight: 200;
    font-family: 'SEGA LOGO FONT', sans-serif;
  }

  button {
    background-color: lightblue;
    color: darkgreen;
    border: none;
    min-width: 20vw;
    min-height: 5vh;
    margin: 10px;
    font-size: 2.5vh;
  }